}
.cell-data > * {
  margin: 0 0.5em;
}
.cell-data > div {
  min-width: 122px;
}
.max-cntnt {
  width: max-content;
}
.r-mgn2 {
  margin-right: 2em;
}
#Citation-cell {
  max-width: 42vw;
}
#InteractionDetails-data-sect #sect-row1 {
  justify-content: flex-start;
}
#InteractionDetails-data-sect #sect-row1 > div {
  margin-right: 5em;
}
