#about-db .first-col {
  flex: 1 1 400px;
  max-width: 100vw;
}
#about-db .second-col {
  flex: 1 0 375px;
  max-width: 100vw;
}
#about-db-how-to-use ul {
  padding-inline-start: 2.5em;
  padding-bottom: 1em;
  padding-top: 0;
}
.db-about-imgs {
  align-items: center;
}
.db-about-imgs>img {
  padding: 0.25em 0 3em 0;
}
#def-pg .first-col {
  flex: 1 0 290px;
  max-width: 100vw;
}
#def-pg .second-col {
  flex: 0 1 824px;
  max-width: 100vw;
}
#add-pubs {
  margin: 1em 0 2em 0;
  height: 40vh;
  width: 100vw;
}
@media screen and (min-width: 1000px) {
  #add-pubs {
    height: 445px;
    width: 100%;
  }
}
#future-dev-pg .first-col,
#pub-sbmt-pg .first-col {
  flex: 2 1 400px;
  max-width: 100vw;
}
#future-dev-pg .second-col,
#pub-sbmt-pg .second-col {
  flex: 1 1 400px;
  max-width: 100vw;
}
@media screen and (max-width: 500px) {
  #pub-sbmt-pg h4 {
    margin: 1em;
  }
}
form[name="appbundle_file_upload"] textarea,
form[name="appbundle_file_upload"] input {
  width: 100%;
}
.two-col-outer .first-col {
  flex: 2 1 420px;
}
.two-col-outer .second-col {
  flex: 1 1 420px;
}
#reg-form-container>form div div {
  justify-content: space-between;
}
#reg-form-container>form div div input,
#reg-form-container>form div div textarea {
  width: 100%;
